.third{
    background-color:gold;
    color: steelblue;
  }
  .cards-wrapper{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  .card{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    height: 200px;
    width: 120px;
    /* background-color:; */
    margin: 10px;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0,0,0,0.25);
    transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  }
  .card-hover{
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.15);
  }
  .image-wrapper{
    width: 110px;
    height: 90px;
    text-align: center;
    padding: 5px;
  }
  img{
    max-width: 100%;
    max-height: 100%;
  }
  
  .skill-title-wrapper{
    padding: 5px;
    height: 100px;
    width: 110px;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: flex-end;
  }
  h4{
    margin:0;
    font-size: 20px;
  }